import { Link } from 'react-router-dom';

const Navbar = () => {
    return (
        <div className="Navbar">
            <div className="home">
                <Link to="/"><h3>MGM</h3></Link>
                
            </div>
          <ul className="menu">
              <li>
                  <Link to="/projects">Projects</Link>
              </li>
              <li>
                  <Link to="/aboutme">About</Link>
              </li>
              <li>
                  <Link to="/contact">Contact</Link>
              </li>
          </ul>
        </div>
    );
}
 
export default Navbar;


